import React, { useState, useEffect, useMemo, useRef, useCallback } from 'react';
import { Connection, PublicKey, LAMPORTS_PER_SOL, clusterApiUrl, Transaction, SystemProgram } from '@solana/web3.js';
import { useConnection, useWallet, WalletProvider, ConnectionProvider } from '@solana/wallet-adapter-react';
import { WalletModalProvider, WalletMultiButton } from '@solana/wallet-adapter-react-ui';
import { PhantomWalletAdapter, SolflareWalletAdapter } from '@solana/wallet-adapter-wallets';
import { WalletAdapterNetwork } from '@solana/wallet-adapter-base'; // Pidejte tento import

import '@solana/wallet-adapter-react-ui/styles.css';

const SKYSCRAPER_ADDRESS = "A7XfU8jrkAkhiw7WwtLtzYJ53n8DUywYkppUr1FyQPZj";

const SkyscraperUI = () => {
  const { connection } = useConnection();
  const { publicKey, sendTransaction } = useWallet();
  const [balance, setBalance] = useState(0);
  const [transactions, setTransactions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [amount, setAmount] = useState(1);
  const [lastSignature, setLastSignature] = useState(null);
  const [hasMore, setHasMore] = useState(true);

  // Observer for infinite scroll
  const observer = useRef();
  const lastElementRef = useCallback(node => {
    if (loading) return;
    if (observer.current) observer.current.disconnect();
    observer.current = new IntersectionObserver(entries => {
      if (entries[0].isIntersecting && hasMore) {
        fetchTransactions(true);
      }
    });
    if (node) observer.current.observe(node);
  }, [loading, hasMore]);

  // Fetch initial data and balance
  const fetchBalance = async () => {
    const pubKey = new PublicKey(SKYSCRAPER_ADDRESS);
    const bal = await connection.getBalance(pubKey);
    setBalance(bal / LAMPORTS_PER_SOL);
  };

  const fetchTransactions = async (isMore = false) => {
    if (!connection || loading) return;
    setLoading(true);

    try {
      const pubKey = new PublicKey(SKYSCRAPER_ADDRESS);
      const options = { limit: 10 };
      if (isMore && lastSignature) {
        options.before = lastSignature;
      }

      const sigs = await connection.getSignaturesForAddress(pubKey, options);
      
      if (sigs.length < 10) setHasMore(false);
      if (sigs.length === 0) {
        setLoading(false);
        return;
      }

      const newLastSig = sigs[sigs.length - 1].signature;
      setLastSignature(newLastSig);

      const details = await Promise.all(
        sigs.map(async (sig) => {
          try {
            const tx = await connection.getParsedTransaction(sig.signature, { maxSupportedTransactionVersion: 0 });
            let change = 0;
            if (tx?.meta) {
              const idx = tx.transaction.message.accountKeys.findIndex(a => 
                (a.pubkey || a).toString() === SKYSCRAPER_ADDRESS
              );
              if (idx !== -1) {
                change = (tx.meta.postBalances[idx] - tx.meta.preBalances[idx]) / LAMPORTS_PER_SOL;
              }
            }
            return { signature: sig.signature, change, timestamp: sig.blockTime };
          } catch (e) { return null; }
        })
      );

      const filtered = details.filter(t => t && t.change > 0);
      setTransactions(prev => isMore ? [...prev, ...filtered] : filtered);
    } catch (e) {
      console.error(e);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchBalance();
    fetchTransactions();
    const interval = setInterval(fetchBalance, 20000);
    return () => clearInterval(interval);
  }, [connection]);

  const buildFloor = async () => {
    if (!publicKey) return alert("PIPOJTE TERMINL!");
    try {
      const transaction = new Transaction().add(
        SystemProgram.transfer({
          fromPubkey: publicKey,
          toPubkey: new PublicKey(SKYSCRAPER_ADDRESS),
          lamports: parseFloat(amount) * LAMPORTS_PER_SOL,
        })
      );
      const signature = await sendTransaction(transaction, connection);
      await connection.confirmTransaction(signature, 'confirmed');
      // Refresh top
      setLastSignature(null);
      setHasMore(true);
      fetchTransactions();
      fetchBalance();
    } catch (e) { alert(e.message); }
  };

  return (
    <div style={styles.container}>
      <div style={styles.scanline}></div>

      <aside style={styles.sidePanel}>
        <div style={styles.glitchContainer}>
          <h1 style={styles.logo}>NEO-CITY</h1>
        </div>
        <div style={styles.statusHardware}>
          <div style={styles.ledGreen}></div>
          <p style={styles.statusText}>SYSTEM: ONLINE</p>
        </div>
        <div style={styles.walletWrapper}>
          <p style={styles.label}>OPERATOR IDENTITY</p>
          <WalletMultiButton className="cyber-button" />
        </div>
        <div style={styles.buildCard}>
          <label style={styles.label}>FLOOR STRENGTH (SOL)</label>
          <input type="number" value={amount} onChange={(e)=>setAmount(e.target.value)} style={styles.input} />
          <button onClick={buildFloor} style={styles.buildBtn}>EXECUTE CONSTRUCTION</button>
        </div>
        <div style={styles.heightCounter}>
          <p style={styles.label}>TOTAL ASCENSION</p>
          <h2 style={styles.solCount}>{balance.toFixed(2)} <span style={{fontSize: '14px'}}>SOL</span></h2>
        </div>
      </aside>

      <main style={styles.towerArea}>
        <div style={styles.gridBackground}></div>
        
        {/* Scrollable Container */}
        <div style={styles.scrollContainer}>
          <div style={styles.skyscraperContainer}>
            <div style={styles.antennaStack}>
              <div style={styles.spire}></div>
              <div style={styles.signalCircle}></div>
            </div>

            <div style={styles.towerBody}>
              {transactions.map((tx, i) => (
                <div 
                  key={tx.signature} 
                  ref={i === transactions.length - 1 ? lastElementRef : null}
                  style={{
                    ...styles.floor,
                    height: Math.min(60 + tx.change * 40, 120),
                    borderTop: tx.change > 0.5 ? '2px solid #0ff' : '1px solid #333',
                  }}
                >
                  <div style={styles.floorCore}>
                    <div style={styles.windowPattern}></div>
                    <div style={styles.neonStripeLeft}></div>
                    <div style={styles.neonStripeRight}></div>
                    <div style={styles.floorLabel}>BLOCK {tx.signature.slice(0, 6)}</div>
                    <div style={styles.floorValue}>+{tx.change.toFixed(3)} SOL</div>
                  </div>
                </div>
              ))}
              {loading && <div style={styles.loadingText}>LOADING DATA_STREAM...</div>}
            </div>

            <div style={styles.foundation}>
               <div style={styles.foundationInner}>BASE_PROTOCOL_V1</div>
            </div>
          </div>
        </div>
      </main>

      <style>{`
        .cyber-button { background: #000 !important; border: 1px solid #0ff !important; width: 100% !important; font-family: 'Courier New', monospace !important; }
        .cyber-button:hover { background: #0ff !important; color: #000 !important; }
        @keyframes scan { from { top: -100px; } to { top: 100%; } }
        @keyframes signal { 0% { transform: scale(1); opacity: 1; } 100% { transform: scale(3); opacity: 0; } }
      `}</style>
    </div>
  );
};

// --- Styles with Scroll additions ---
const styles = {
  container: { background: '#050508', height: '100vh', display: 'flex', color: '#0ff', fontFamily: '"Courier New", monospace', overflow: 'hidden' },
  scanline: { position: 'absolute', top: 0, left: 0, width: '100%', height: '4px', background: 'rgba(0, 255, 255, 0.1)', zIndex: 100, pointerEvents: 'none', animation: 'scan 4s linear infinite' },
  sidePanel: { width: '350px', background: 'rgba(10, 10, 15, 0.95)', borderRight: '2px solid #0ff', padding: '30px', display: 'flex', flexDirection: 'column', zIndex: 10 },
  logo: { fontSize: '32px', letterSpacing: '8px', margin: '0 0 10px 0', textShadow: '2px 2px #f0f' },
  statusHardware: { display: 'flex', alignItems: 'center', gap: '10px', marginBottom: '40px' },
  ledGreen: { width: '8px', height: '8px', borderRadius: '50%', background: '#0f0', boxShadow: '0 0 8px #0f0' },
  statusText: { fontSize: '10px', margin: 0 },
  label: { fontSize: '10px', color: '#555', marginBottom: '8px' },
  walletWrapper: { marginBottom: '30px' },
  buildCard: { background: 'rgba(0, 255, 255, 0.03)', padding: '20px', border: '1px solid #1a1a1a', marginBottom: '30px' },
  input: { width: '100%', background: '#000', border: '1px solid #0ff', color: '#0ff', padding: '12px', marginBottom: '15px' },
  buildBtn: { width: '100%', padding: '15px', background: 'transparent', color: '#0ff', border: '1px solid #0ff', cursor: 'pointer' },
  heightCounter: { marginTop: 'auto', borderTop: '1px solid #1a1a1a', paddingTop: '20px' },
  solCount: { fontSize: '36px', margin: 0 },
  towerArea: { flex: 1, position: 'relative', overflow: 'hidden' },
  gridBackground: { position: 'absolute', width: '100%', height: '100%', backgroundImage: 'linear-gradient(#111 1px, transparent 1px), linear-gradient(90deg, #111 1px, transparent 1px)', backgroundSize: '40px 40px' },
  
  // NEW SCROLL STYLES
  scrollContainer: { height: '100%', overflowY: 'auto', padding: '100px 0', scrollbarWidth: 'none' },
  skyscraperContainer: { display: 'flex', flexDirection: 'column', alignItems: 'center', width: '100%' },
  towerBody: { display: 'flex', flexDirection: 'column', width: '280px', gap: '6px' },
  loadingText: { textAlign: 'center', fontSize: '12px', padding: '20px', color: '#f0f' },
  
  floor: { width: '100%', background: 'rgba(0, 20, 30, 0.8)', position: 'relative' },
  floorCore: { width: '100%', height: '100%', position: 'relative', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' },
  windowPattern: { position: 'absolute', width: '100%', height: '100%', backgroundImage: 'radial-gradient(#0ff 10%, transparent 10%)', backgroundSize: '12px 12px', opacity: 0.15 },
  neonStripeLeft: { position: 'absolute', left: '0', top: 0, bottom: 0, width: '3px', background: '#0ff', boxShadow: '0 0 10px #0ff' },
  neonStripeRight: { position: 'absolute', right: '0', top: 0, bottom: 0, width: '3px', background: '#f0f', boxShadow: '0 0 10px #f0f' },
  floorLabel: { fontSize: '9px', color: '#555', zIndex: 2 },
  floorValue: { fontSize: '14px', fontWeight: 'bold', zIndex: 2 },
  antennaStack: { height: '100px', width: '4px', background: '#0ff', position: 'relative' },
  spire: { position: 'absolute', top: 0, left: '-3px', width: '10px', height: '10px', background: '#f0f', borderRadius: '50%' },
  signalCircle: { position: 'absolute', top: '0', left: '-13px', width: '30px', height: '30px', border: '1px solid #f0f', borderRadius: '50%', animation: 'signal 2s infinite' },
  foundation: { width: '340px', height: '80px', background: '#000', border: '4px solid #1a1a1a', display: 'flex', alignItems: 'center', justifyContent: 'center', marginTop: '10px' },
  foundationInner: { border: '1px solid #333', padding: '5px 20px', fontSize: '12px', color: '#333' }
};


export const SolanaSkyscraperApp = () => {
  // 1. Definujeme s jako Mainnet-Beta
  const network = WalletAdapterNetwork.Mainnet;

  // 2. Vytvome endpoint (bu clusterApiUrl, nebo vlastn URL)
  const endpoint = useMemo(() => 'https://mainnet.helius-rpc.com/?api-key=9d795b67-37ce-48af-8b54-d8ef4cf8c43b', []);

  // 3. Inicializace penenek pro Mainnet
  const wallets = useMemo(() => [
    new SolflareWalletAdapter(),
    new PhantomWalletAdapter(), // Doporuuji pidat i Phantom pro Mainnet uivatele
  ], []);

  return (
    <ConnectionProvider endpoint={endpoint}>
      <WalletProvider wallets={wallets} autoConnect>
        <WalletModalProvider>
          <SkyscraperUI />
        </WalletModalProvider>
      </WalletProvider>
    </ConnectionProvider>
  );
};

export default SolanaSkyscraperApp;